.clock {
  max-width: 500px;
  margin: 100px auto;
  text-align: center;
  font-size: 60px;
  font-family: Courier, monospace;
}

.alarm {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 20px;
  -ms-flex-pack: center;
      justify-content: center;
}

.delete {
  color: darkred;
  cursor: pointer;
  margin-left: 60px;
}

.delete::after {
  content: '\2716';
}

.reset {
  margin-left: 60px;
}



